import React, { useEffect, useCallback, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { useTranslation, Trans } from 'react-i18next';
import usePortal from 'react-useportal';

// Form
import { useForm, Controller } from 'react-hook-form';
import { useStateMachine } from 'little-state-machine';
import { yupResolver } from '@hookform/resolvers';
import * as Yup from 'yup';

// Update Action
import { resetStore, updateAction } from 'utils/wizard';

// Header Control
import useHeaderContext from 'hooks/useHeaderContext';

// Components
import WizardButtons from 'components/WizardButtons';
import Dropdown from 'components/Dropdown';
import Link from 'components/Link';

// Helpers
import { isLanguage } from 'helper/isLanguageHelper';
import { scrollToTop } from 'helper/scrollHelper';

// Assets
import HeaderSplash from 'assets/images/baseLogoSplash.png';

// Data
import { countryData, CountryDataProps } from 'data/country';
import { languageData } from 'data/lang';

// Styles
import {
  HeaderImageContainer,
  HeaderImage,
  LogoWhiteBG,
  WelcomeContent,
  WelcomeStyledFormAlternative,
  BoldBlackText,
  WelcomeNote,
  BoldBlackTextPrivacy,
  CheckSVG,
  WelcomeSelect,
} from '../style';

const schema = Yup.object().shape({
  language: Yup.string().required(),
  country: Yup.string().required(),
  // accessCode: Yup.string(),
}).defined();

type Step1Type = Yup.InferType<typeof schema>;

// TO-DO: Remove the hardcoded value when we decide to add more locales. We only need Pakistan.
const findLocaleBy = countryData.filter((country: CountryDataProps) => country.value === 'Pakistan' || country.value === 'Nigeria');

const Step1 = (p: Wizard.StepProps) => {
  const { Portal } = usePortal({
    bindTo: document && document.getElementById('wizard-buttons') as HTMLDivElement,
  });
  const resetExecuted = React.useRef(true);

  const { state, actions } = useStateMachine({ update: updateAction(p.storeKey), reset: resetStore() });

  const store = state?.[p.storeKey];

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [activeStep, setActiveStep] = useState(true);
  const {
    setType, setDoGoBack, setLogoSize, setSubtitle,
  } = useHeaderContext();

  const history = useHistory();

  const {
    control,
    formState,
    handleSubmit,
    reset,
    // watch,
  } = useForm({
    defaultValues: store,
    resolver: yupResolver(schema),
    mode: 'onChange',
  });

  const { isValid } = formState;

  const onSubmit = async (values: Step1Type) => {
    if (values) {
      actions.update(values);
      if (p.nextStep) {
        setActiveStep(false);
        history.push(p.nextStep);
      }
    }
  };

  const doBack = useCallback(() => {
    if (p.previousStep) {
      setActiveStep(false);
      history.push(p.previousStep);
    } else {
      history.goBack();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    scrollToTop();
    setDoGoBack(() => doBack);
    setLogoSize('regular');
    setType('null');
    setSubtitle('');
  }, [doBack, setDoGoBack, setLogoSize, setType, setSubtitle]);

  useEffect(() => {
    if (resetExecuted.current) {
      resetExecuted.current = false;
      actions.reset({ welcome: {} });
      reset({ patientId: '' });
    }
  }, [actions.reset, actions, reset]);

  const { t, i18n } = useTranslation();

  // const lang = watch('language');

  React.useEffect(() => {
    i18n.changeLanguage('en');
  }, [i18n]);

  return (
    <WelcomeStyledFormAlternative>
      <HeaderImageContainer>
        <HeaderImage
          src={HeaderSplash}
        />
        <LogoWhiteBG className="logoWhiteBg" />
      </HeaderImageContainer>

      {/* Language */}
      <BoldBlackText>
        {t('main:selectYourLanguage', 'Language')}
      </BoldBlackText>

      <Controller
        control={control}
        name="language"
        defaultValue={i18n.language.split('-')[0] || languageData[0].value}
        render={({ onChange, value }) => (
          <Dropdown
            disabled
            onChange={e => onChange(e.currentTarget.value)}
            value={value}
          >
            {
              languageData.map(({ value: values, label }) => (
                <option
                  key={values}
                  id={values}
                  value={values}
                >
                  {label}
                </option>
              ))
            }
          </Dropdown>
        )}
      />

      {/* Location */}
      <BoldBlackText>
        {t('main:selectYourLocation', 'Location')}
      </BoldBlackText>

      <Controller
        control={control}
        name="country"
        render={({ onChange, value: valueController }) => (
          <WelcomeSelect
            options={findLocaleBy}
            onChange={(e: any) => { onChange(e?.value); }}
            value={findLocaleBy.filter(({ value }) => value === valueController)}
            className="custom-select"
            classNamePrefix="custom-select"
            placeholder={t('main:selectYourLocationPlaceholder', 'Select your location')}
          />
        )}
      />

      <WelcomeContent>
        <WelcomeNote>
          <Trans i18nKey="main:note">
            <strong>Please note:</strong> This form is for data collection only.
            It will not predict your COVID-19 status or diagnose any disease, disorder, or other health condition.
            Virufy is conducting research and will use the information you provide for research.
            The Virufy app doesn&apos;t replace a doctor. Remember that it is your responsibility to
            seek medical advice from your doctor.
          </Trans>
        </WelcomeNote>

        <BoldBlackTextPrivacy>
          <Trans
            i18nKey="main:privacyPolicy"
            components={{
              link1: <Link to="https://virufy.org/consent_form/" target="_blank">Consent form</Link>,
              link2: <Link to="https://virufy.org/privacy_policy/" target="_blank"> Privacy Policy</Link>,
            }}
          >
            By proceeding you accept the terms of our
          </Trans>
        </BoldBlackTextPrivacy>
        {activeStep && (
          <Portal>
            <WizardButtons
              invert
              leftSide={isLanguage(i18n.language, ['ur']) ? <CheckSVG /> : t('helpVirufy:nextButton')}
              leftHandler={handleSubmit(onSubmit)}
              leftDisabled={!isValid}
            />
          </Portal>
        )}
      </WelcomeContent>
    </WelcomeStyledFormAlternative>
  );
};

export default React.memo(Step1);
